import yargs from 'yargs';
import LoginCommand from './commands/login';

yargs
	.usage('Usage: $0 <command> [options]')
	.command(LoginCommand)
	// command #2 requiress only '-c'
	.command('migrate', 'Migrate table')
	.example('$0 migrate -c config.json', 'Migrate table')

	// at least one command is required
	.demand(1, 'Please specify one of the commands!')
	.strict()

	// either command requires -c option
	.option('c', {
		alias: 'conf',
		type: 'string',
		demand: 'Please specify config file',
		//nargs: 1,
		//default: 'edm.config.json',
		describe: 'Configuration JSON file'
	})

	.help('h')
	.alias('h', 'help')
	.wrap(null)
	.epilog('Timely CLI- No more excuses!')
	.argv;
